:root{
	--basewidth:800px;
}
#tabcontainer{
	height: 50px;
  position: relative;
  margin-top: -50px;
  margin-bottom: 50px;
  padding: 0;
  font-weight: bold;
  list-style: none;
  text-transform: uppercase;
}
.tab{
  border-left: 1px solid #aaa;
    text-decoration: none;
    padding: 0 6px;
    float: left;
    width: 25%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
	font-size: 0.8em;
    font-weight: bold;
    line-height: 30px;
    background: #bbb;
    position: relative;
    float:left;
    cursor:pointer;
}
.tab.current{

      background-color:#ddd;
}
.tab.current:after{
	 content:"";
	  border: 15px solid transparent;
      display: block;
      margin-left: 0;
      position: absolute;
      left: 0;
      top: 0;
      border-left: 20px solid orange;
}
.formbase{
	//overflow-x:hidden;
	width:var(--basewidth);
	position:relative;
	padding:20px;
	margin:80px auto;
	background-color:#aaa;
	min-height:300px;
	border-radius:10px;
}
.pagewrapper{
	position:relative;
}
.page{
	position:absolute;
}
.page.next{
	opacity:0;
	left:calc(var(--basewidth) + 150px);
}
.page.current{
	opacity:1;	
	display:grid;
	left:100px;
	grid-column-gap:10px;
	grid-row-gap:10px;
	grid-template-columns:repeat(2,100px);
	grid-template-rows:repeat(auto-fit,100px);
}
.page.prev{
	opacity:0;
}
.page.incoming{
	animation:slide-in 1s forwards;
	-webkit-animation:slide-in 1s forwards;
}
.page.outgoing{
	animation:slide-out 1s forwards;
	-webkit-animation:slide-out 1s forwards;
}
.optionlink{
	height:auto-fit;
}
.optionradio{
}
#nextbtn{
position:absolute;
bottom: 20px;
	  right: 20px;
}
#prevbtn{
position:absolute;
bottom: 20px;
	  left: 20px;
}
#subbtn{
	position:absolute;
	bottom:20px;
	right:20px;
	display:none;
}
.requestoption{
	width:100px;
	height:100px;
	background-color:green;
	margin:10px; 
	border-radius:20px;
}
@keyframes slide-in {
100% { -webkit-transform: translateX(-100%); transform: translateX(-100%);
}
}
@-webkit-keyframes slide-in {
100% { -webkit-transform: translateX(-100%);
}
}
@keyframes slide-out {
100% { -webkit-transform: translateX(-200%); transform: translateX(-200%);
}
}
@-webkit-keyframes slide-out {
100% { -webkit-transform: translateX(-200%);
}
}














































